const API_BASE_URL = 'https://taskdrop-render-backend.onrender.com';
import { getUserData } from '../utils/userHelper';

//   API  (   )
export interface ApiTask {
  id: string;
  title: string;
  description?: string;
  done: boolean;
  created_at: string;
  priority: string;
  due_date?: string;
  due_time?: string;
  notification?: string;
  tags?: string[];
  attachments?: string[];
  notes?: string;
  location?: string;
  repeat?: string;
  user_id?: number;
}

class ApiClient {
  private userId: number | null = null;

  //    ID 
  private async getUserId(): Promise<number> {
    if (this.userId !== null) {
      return this.userId;
    }

    try {
      const userData = await getUserData();
      this.userId = userData.id;
      console.log(' ID :', this.userId);
      
      //  ID  localStorage   
      try {
        localStorage.setItem('user_telegram_id', String(this.userId));
      } catch (e) {
        console.warn('   ID  localStorage:', e);
      }
      
      return this.userId;
    } catch (error) {
      console.error('   ID :', error);
      
      //   ID  localStorage
      try {
        const savedId = localStorage.getItem('user_telegram_id');
        if (savedId) {
          const parsedId = parseInt(savedId, 10);
          if (!isNaN(parsedId) && parsedId > 0) {
            console.log(' ID   localStorage:', parsedId);
            this.userId = parsedId;
            return parsedId;
          }
        }
      } catch (e) {
        console.warn('   ID  localStorage:', e);
      }
      
      //     ,   ID
      const randomId = Math.floor(Math.random() * 1000000) + 3000;
      console.warn('  ID :', randomId);
      this.userId = randomId;
      return randomId;
    }
  }

  private async request<T>(endpoint: string, options: RequestInit = {}): Promise<T> {
    try {
      console.log('API Request:', {
        url: `${API_BASE_URL}${endpoint}`,
        method: options.method || 'GET',
        body: options.body ? JSON.parse(options.body as string) : undefined
      });

      const response = await fetch(`${API_BASE_URL}${endpoint}`, {
        ...options,
        headers: {
          'Content-Type': 'application/json',
          ...options.headers,
        },
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        console.error('API Error Response:', {
          status: response.status,
          statusText: response.statusText,
          errorData
        });
        throw new Error(errorData.message || `API request failed: ${response.statusText}`);
      }

      const data = await response.json();
      console.log('API Success Response:', data);
      return data;
    } catch (error) {
      console.error('API request error:', error);
      throw error;
    }
  }

  //   
  async getTasks(): Promise<ApiTask[]> {
    const userId = await this.getUserId();
    return this.request<ApiTask[]>(`/tasks?user_id=${userId}`);
  }

  //    ,    ID=1
  async getAllUserTasks(): Promise<ApiTask[]> {
    try {
      const userId = await this.getUserId();
      //    Telegram ID
      const tasks = await this.request<ApiTask[]>(`/tasks?user_id=${userId}`);
      
      //  ID    1,     ID=1
      if (userId !== 1) {
        try {
          const legacyTasks = await this.request<ApiTask[]>(`/tasks?user_id=1`);
          if (legacyTasks && legacyTasks.length > 0) {
            console.log(`    user_id=1:`, legacyTasks);
            return [...tasks, ...legacyTasks];
          }
        } catch (e) {
          console.warn("    :", e);
        }
      }
      
      return tasks;
    } catch (error) {
      console.error("   :", error);
      throw error;
    }
  }

  //   
  async createTask(task: Omit<ApiTask, 'id' | 'created_at'>): Promise<ApiTask> {
    const userId = await this.getUserId();
    
    const response = await this.request<{ id: string; success: boolean }>('/tasks', {
      method: 'POST',
      body: JSON.stringify({
        user_id: userId,
        title: task.title,
        description: task.description,
        due_date: task.due_date,
        due_time: task.due_time,
        notification: task.notification,
        priority: task.priority,
        tags: task.tags,
        attachments: task.attachments,
        notes: task.notes,
        location: task.location,
        repeat: task.repeat,
        completed: task.done || false
      }),
    });

    //   ,      ID
    return {
      id: response.id,
      title: task.title,
      description: task.description,
      due_date: task.due_date,
      due_time: task.due_time,
      notification: task.notification,
      priority: task.priority,
      tags: task.tags,
      attachments: task.attachments,
      notes: task.notes,
      location: task.location,
      repeat: task.repeat,
      done: task.done || false,
      created_at: new Date().toISOString(),
      user_id: userId
    };
  }

  //  
  async updateTask(id: string, task: Partial<Omit<ApiTask, 'id' | 'created_at'>>): Promise<ApiTask> {
    console.log('    :', { id, task });
    
    //    ,   ,  id  created_at
    const updateData = { ...task };
    
    console.log(' :', updateData);
    
    try {
      //     
      return await this.request<ApiTask>(`/tasks/${id}`, {
        method: 'PUT',
        body: JSON.stringify(updateData),
      });
    } catch (error) {
      //     (,     completed),
      //    -      
      console.warn('   ,   :', error);
      
      //   ,    
      const currentTask = await this.getTask(id);
      
      //  ID 
      const userId = await this.getUserId();
      
      //      
      const newTaskData = {
        ...currentTask,
        ...task,
        user_id: userId
      };
      
      //     id  created_at
      const { id: _, created_at: __, ...taskDataToCreate } = newTaskData;
      
      const newTask = await this.createTask(taskDataToCreate);
      
      //   
      await this.deleteTask(id);
      
      return newTask;
    }
  }

  //     ID
  async getTask(id: string): Promise<ApiTask> {
    try {
      //      API    
      console.log(`       ID: ${id}`);
      const task = await this.request<ApiTask>(`/tasks/${id}`);
      return task;
    } catch (error) {
      console.warn(`      ID ${id},      :`, error);
      
      //     ,      
      const tasks = await this.getTasks();
      console.log(`   ID ${id}    ${tasks.length} `);
      
      //   ID     
      const task = tasks.find(t => String(t.id) === String(id));
      
      if (!task) {
        console.error(`  ID ${id}     ,    `);
        throw new Error(`Task with id ${id} not found`);
      }
      
      return task;
    }
  }

  //  
  async deleteTask(id: string): Promise<void> {
    await this.request(`/tasks/${id}`, {
      method: 'DELETE',
    });
  }

  //   
  async toggleTaskComplete(id: string, completed: boolean): Promise<ApiTask> {
    return this.updateTask(id, { done: completed });
  }
}

export const apiClient = new ApiClient(); 