import { isTMA } from '@telegram-apps/sdk-react';
import axios from 'axios';

interface UserData {
  id: number;
  first_name: string;
  last_name?: string;
  username?: string;
  photo_url?: string;
  telegram_id?: number;
}

// API URL     
const API_BASE_URL = 'https://taskdrop-render-backend.onrender.com';

//     Telegram WebApp
declare global {
  interface Window {
    Telegram?: {
      WebApp?: {
        initDataUnsafe: {
          user?: {
            id: number;
            first_name: string;
            last_name?: string;
            username?: string;
            photo_url?: string;
          };
        };
        initData: string;
        ready: () => void;
        MainButton: {
          text: string;
          isVisible: boolean;
          onClick: (callback: () => void) => void;
          show: () => void;
          hide: () => void;
        };
      };
    };
  }
}

/**
 *     Telegram WebApp     
 */
export const getUserData = async (): Promise<UserData> => {
  try {
    //       
    console.log(' Telegram :', Boolean(window.Telegram));
    console.log(' WebApp :', Boolean(window.Telegram?.WebApp));
    
    // ,     Telegram
    const isTelegram = await isTMA('complete');
    console.log('isTMA :', isTelegram);
    
    //    Telegram WebApp
    const telegramWebApp = window.Telegram?.WebApp;
    
    if (telegramWebApp && telegramWebApp.initDataUnsafe && telegramWebApp.initDataUnsafe.user) {
      const telegramUser = telegramWebApp.initDataUnsafe.user;
      console.log('    Telegram:', telegramUser);
      
      try {
        // ,      
        const response = await axios.get(`${API_BASE_URL}/users/telegram/${telegramUser.id}`);
        
        //   ,   
        if (response.status === 200 && !response.data.error) {
          console.log('   :', response.data);
          
          // ,   ID   Telegram ID
          //  ,    
          if (response.data.id !== telegramUser.id) {
            console.log('ID     Telegram ID, ...');
            try {
              const updateResponse = await axios.put(`${API_BASE_URL}/users/${response.data.id}`, {
                id: telegramUser.id,
                telegram_id: telegramUser.id
              });
              if (updateResponse.status === 200) {
                console.log('ID  :', updateResponse.data);
                
                //  ID  localStorage   
                try {
                  localStorage.setItem('user_telegram_id', String(telegramUser.id));
                } catch (e) {
                  console.warn('   ID  localStorage:', e);
                }
                
                return updateResponse.data;
              }
            } catch (updateError) {
              console.error('   ID:', updateError);
            }
          }
          
          //  ID  localStorage   
          try {
            localStorage.setItem('user_telegram_id', String(telegramUser.id));
          } catch (e) {
            console.warn('   ID  localStorage:', e);
          }
          
          return response.data;
        }
      } catch (error) {
        console.log('    ,  ');
        
        try {
          //     ID = Telegram ID
          const userData = {
            id: telegramUser.id,
            telegram_id: telegramUser.id,
            first_name: telegramUser.first_name,
            last_name: telegramUser.last_name,
            username: telegramUser.username,
            photo_url: telegramUser.photo_url
          };
          
          const createResponse = await axios.post(`${API_BASE_URL}/users`, userData);
          
          if (createResponse.status === 200 || createResponse.status === 201) {
            console.log('  :', createResponse.data);
            
            //  ID  localStorage   
            try {
              localStorage.setItem('user_telegram_id', String(telegramUser.id));
            } catch (e) {
              console.warn('   ID  localStorage:', e);
            }
            
            return createResponse.data;
          }
        } catch (createError) {
          console.error('   :', createError);
        }
      }
      
      //      ,   
      //  ID  localStorage   
      try {
        localStorage.setItem('user_telegram_id', String(telegramUser.id));
      } catch (e) {
        console.warn('   ID  localStorage:', e);
      }
      
      return {
        id: telegramUser.id, //  telegram_id  id
        first_name: telegramUser.first_name,
        last_name: telegramUser.last_name,
        username: telegramUser.username,
        photo_url: telegramUser.photo_url,
        telegram_id: telegramUser.id
      };
    }
    
    //    Telegram    ,   ID   
    console.log('      Telegram,   ');
    
    //   ID  localStorage
    let savedTelegramId = null;
    try {
      const savedId = localStorage.getItem('user_telegram_id');
      if (savedId) {
        savedTelegramId = parseInt(savedId, 10);
        console.log(' ID  localStorage:', savedTelegramId);
      }
    } catch (e) {
      console.warn('   ID  localStorage:', e);
    }
    
    //    ID,     
    if (savedTelegramId) {
      try {
        const response = await axios.get(`${API_BASE_URL}/users/telegram/${savedTelegramId}`);
        if (response.status === 200 && !response.data.error) {
          console.log('      ID:', response.data);
          return response.data;
        }
      } catch (e) {
        console.warn('      ID:', e);
      }
    }
    
    //      ,   
    console.error('  .   .');
    
    //   ID   
    const temporaryId = Math.floor(Math.random() * 1000000) + 1000; 
    
    return {
      id: temporaryId,
      first_name: '',
      last_name: '',
      username: 'guest_user',
      photo_url: 'https://i.pravatar.cc/300?u=guest',
      telegram_id: temporaryId
    };
  } catch (error) {
    console.error('Error getting user data:', error);
    
    //   ID  localStorage   
    let savedTelegramId = null;
    try {
      const savedId = localStorage.getItem('user_telegram_id');
      if (savedId) {
        savedTelegramId = parseInt(savedId, 10);
        console.log(' ID  localStorage   :', savedTelegramId);
        
        return {
          id: savedTelegramId,
          first_name: '',
          last_name: '',
          username: 'recovered_user',
          photo_url: 'https://i.pravatar.cc/300?u=recovered',
          telegram_id: savedTelegramId
        };
      }
    } catch (e) {
      console.warn('   ID  localStorage   :', e);
    }
    
    //     ,   ID
    const emergencyId = Math.floor(Math.random() * 1000000) + 2000;
    
    //           ID
    return {
      id: emergencyId,
      first_name: '',
      last_name: '',
      username: 'emergency_user',
      photo_url: 'https://i.pravatar.cc/300?u=emergency',
      telegram_id: emergencyId
    };
  }
};

