/**
 *  Telegram ID   
 */
export const getTelegramDebugInfo = (): { telegramId: number | null, webAppAvailable: boolean, userId: number | null } => {
  try {
    const telegramWebApp = window.Telegram?.WebApp;
    const webAppAvailable = Boolean(telegramWebApp);
    const telegramUser = telegramWebApp?.initDataUnsafe?.user;
    const telegramId = telegramUser?.id || null;
    
    //    userId  localStorage
    let userId = null;
    try {
      const userData = localStorage.getItem('userData');
      if (userData) {
        const parsed = JSON.parse(userData);
        userId = parsed?.id || null;
      }
    } catch (e) {
      console.error('   userId  localStorage:', e);
    }
    
    return {
      telegramId,
      webAppAvailable,
      userId
    };
  } catch (error) {
    console.error('    Telegram ID:', error);
    return {
      telegramId: null,
      webAppAvailable: false,
      userId: null
    };
  }
}; 